import {
  animate,
  animateChild,
  group,
  query,
  style,
  transition,
  trigger,
} from '@angular/animations';

export const slideInAnimation = trigger('routeAnimations', [
  transition('One1 => One, Two1 => Two, Three1 => Three, Three2 => Three1, * => Four', [
    style({ position: 'relative' }),
    query(
      ':enter .mat-fab-container',
      style({
        opacity: 0,
        position: 'fixed'
      })
    ),
    query(
      ':leave .mat-fab-container',
      style({
        opacity: 1,
        position: 'fixed'
      })
    ),
    query(':enter, :leave', [
      style({
        position: 'absolute',
        top: 0,
        left: 0,
        width: '100%',
      }),
    ]),
    query(':enter', [style({ left: '-100%' })]),
    query(':leave', animateChild()),
    group([
      query(':leave', [animate('300ms ease-out', style({ left: '100%' }))]),
      query(':enter', [animate('300ms ease-out', style({ left: '0%' }))]),
      query(':enter .mat-fab-container', animate(300, style({ opacity: 1 }))),
      query(':leave .mat-fab-container', animate(300, style({ opacity: 0 }))),
    ]),
    query(':enter', animateChild()),
  ]),
  transition('One => One1, Two => Two1, Three => Three1, Three1 => Three2, Four => *', [
    style({ position: 'relative' }),
    query(
      ':enter .mat-fab-container',
      style({
        opacity: 0,
        position: 'fixed'
      })
    ),
    query(
      ':leave .mat-fab-container',
      style({
        opacity: 1,
        position: 'fixed'
      })
    ),
    query(':enter, :leave', [
      style({
        position: 'absolute',
        top: 0,
        left: 0,
        width: '100%',
      }),
    ]),
    query(':enter', [style({ left: '100%' })]),
    query(':leave', animateChild()),
    group([
      query(':leave', [animate('200ms ease-out', style({ left: '-100%' }))]),
      query(':enter', [animate('300ms ease-out', style({ left: '0%' }))]),
      query(':enter .mat-fab-container', animate(300, style({ opacity: 1 }))),
      query(':leave .mat-fab-container', animate(300, style({ opacity: 0 }))),
    ]),
    query(':enter', animateChild()),
  ]),
]);
